import projectsData from '../data/projects';

export default class ProjectsAPI {
  constructor() {
    this.page = 1;
    this.perPage = 3;
    this.lastPage = Math.floor(projectsData.length / this.perPage);
  }

  fetchProjects() {
    if (this.page === this.lastPage) {
      return new Promise((resolve, reject) => {
        // * Ð†Ð¼Ñ–Ñ‚Ð°Ñ†Ñ–Ñ Ð²Ñ–Ð´Ð¿Ð¾Ð²Ñ–Ð´Ñ– Ð²Ñ–Ð´ ÑÐµÑ€Ð²ÐµÑ€Ð°
        setTimeout(() => {
          resolve(
            projectsData.slice(
              this.page * this.perPage - this.perPage,
              projectsData.length
            )
          );
        }, 500);
      });
    }

    return new Promise((resolve, reject) => {
      // * Ð†Ð¼Ñ–Ñ‚Ð°Ñ†Ñ–Ñ Ð²Ñ–Ð´Ð¿Ð¾Ð²Ñ–Ð´Ñ– Ð²Ñ–Ð´ ÑÐµÑ€Ð²ÐµÑ€Ð° (Ð· Ð·Ð°Ñ‚Ñ€Ð¸Ð¼ÐºÐ¾ÑŽ)
      setTimeout(() => {
        resolve(
          projectsData.slice(
            this.page * this.perPage - this.perPage,
            this.page * this.perPage
          )
        );
      }, 500);
    });

    // * ÐœÐ¾Ð¼ÐµÐ½Ñ‚Ð°Ð»ÑŒÐ½Ð° Ð²Ñ–Ð´Ð¿Ð¾Ð²Ñ–Ð´ÑŒ
    // return Promise.resolve(
    //   projectsData.slice(
    //     this.page * this.perPage - this.perPage,
    //     this.page * this.perPage
    //   )
    // ).then(projects => {
    //   this.incrementPage();
    //   return projects;
    // });
  }

  incrementPage() {
    this.page += 1;
  }

  resetPage() {
    this.page = 1;
  }
}
